.site-nav {
  background-color: #bbbbbb;
  text-align: center;
  &__menu {
    text-align: center;
    @include dib; }
  li {
    @include dib;
    padding-top: 19px;
    padding-bottom: 14px;
    & + li {
      margin-left: 50px; }
    a {
      color: #000000;
      font-family: "PF Din Text Cond Pro";
      font-size: 22px;
      font-weight: 400;
      line-height: 32px;
      text-transform: uppercase;
      text-decoration: none;
      &:hover {
        color: #fff; }
      &:active {
        color: #9f9f9f; } } }
  &__cart {
    float: right;
    color: #ffffff;
    font-family: "PF Din Text Cond Pro";
    font-size: 22px;
    font-weight: 400;
    &-link {
      @include dib;
      background-color: #fc2db7;
      padding: 19px 20px;
      margin-right: -3px;
      text-transform: uppercase;
      color: #ffffff;
      &:hover {
        background-color: #828282;
        text-decoration: none; }
      &:active {
        background-color: #9f9f9f; } }
    &-info {
      @include dib;
      background-color: #fc2db7;
      padding: 9px 15px;
      font-size: 18px;
      text-align: left;
      a {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-decoration: underline;
        &:hover {
          text-decoration: none; }
        &:active {
          color: #9f9f9f;
          text-decoration: none; } } } } }

