.site-header {
  margin-top: 20px;
  margin-bottom: 40px;
  &__logo {
    @include dib;
    margin-right: 20px; }
  .slogan {
    @include dib;
    margin-top: 80px;
    color: #444444;
    font-family: "PF Din Text Cond Pro";
    font-weight: 400;
    text-transform: uppercase;
    &__text-border {
      display: inline-block;
      font-size: 22px;
      padding-top: 20px;
      border-top: 1px solid #9f9f9f; }
    &__text-big {
      font-size: 30px;
      line-height: 40px; } }
  &__right {
    float: right;
    @include dib; }
  &__login {
    text-align: right;
    margin-bottom: 15px;
    margin-right: 20px;
    li {
      display: inline-block;
      vertical-align: middle;
      & + li:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 1px;
        height: 30px;
        background-color: #9f9f9f;
        margin-left: 10px;
        margin-right: 15px; } }
    a {
      height: 17px;
      color: #000000;
      font-family: "PF Din Text Cond Pro";
      font-size: 18px;
      font-weight: 400;
      &:active {
        color: #9f9f9f; } } }
  &__contacts {
    margin-bottom: 10px; }
  &__phone {
    @include dib;
    margin-right: 25px;
    .icon {
      display: inline-block;
      @include s(icon-phone);
      margin-right: 8px; }
    a {
      font-family: "PF Din Text Cond Pro";
      font-weight: 400;
      text-transform: uppercase;
      color: #444444;
      font-size: 22px;
      strong {
        color: #000000;
        font-size: 26px;
        font-weight: 400; } } }
  &__email {
    display: inline-block;
    vertical-align: bottom;
    a {
      color: #000;
      font-family: "PF Din Text Cond Pro";
      font-size: 26px;
      font-weight: 400;
      &:active {
        color: #9f9f9f; } } }
  &__social {
    @include dib;
    margin-right: 50px;
    margin-left: 180px;
    li {
      @include dib;
      & + li {
        margin-left: 25px; }
      .icon {
        content: '';
        @include dib;
        @each $cls in inst, fb, vk {
          &.icon-#{$cls} {
            @include s(icon-#{$cls});
            &:hover {
              @include s(icon-#{$cls}--hover); }
            &:active {
              @include s(icon-#{$cls}--release); } } } } } }
  &__question {
    margin-bottom: 20px;
    @include dib;
    a {
      color: #000000;
      font-family: "PF Din Text Cond Pro";
      font-size: 22px;
      font-weight: 400;
      &:hover {
        .icon {
          @include s(icon-question--hover); } }
      &:active {
        color: #9f9f9f;
        .icon {
          @include s(icon-question--release); } }
      .icon {
        content: '';
        margin-right: 15px;
        display: inline-block;
        vertical-align: bottom;
        @include s(icon-question); } } }
  &__search {
    input[type='search'] {
      width: 498px;
      height: 40px;
      background-color: #ffffff;
      padding-left: 10px;
      padding-right: 10px;
      font-family: "PF Din Text Cond Pro";
      font-size: 16px;
      font-weight: 400; }
    input[type='submit'] {
      height: 40px;
      background-color: #828282;
      color: #eeeeee;
      font-family: "PF Din Text Cond Pro";
      font-size: 22px;
      font-weight: 400;
      padding: 10px 15px;
      vertical-align: top;
      &:hover {
        background-color: #fc2db7; }
      &:active {
        background-color: #9f9f9f; } } } }

