.fav {
  margin-top: 40px;
  &__list {
    @include dib;
    width: 280px;
    margin-left: 40px;
    margin-right: 20px;
    ul {
      margin-top: 10px; } }
  &__item {
    @include clr;
    margin-bottom: 20px;
    color: #000000;
    font-family: "PF Din Text Cond Pro";
    font-size: 18px;
    font-weight: 400; }
  &__img {
    margin-right: 10px;
    max-width: 130px;
    @include dib; }
  &__link {
    @include dib;
    max-width: 130px;
    padding-top: 8px;
    border-top: 1px solid  #9f9f9f;
    color: #000000;
    &:active {
      color: #9f9f9f; } } }

