$icons: map-merge($icons,('icon-fb': (X: -88px, Y:0px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-inst--hover': (X: -88px, Y:-44px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-inst--release': (X: 0px, Y:-88px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-inst': (X: 0px, Y:0px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-phone': (X: 0px, Y:-132px, W: 34px, H: 28px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-question--hover': (X: -132px, Y:0px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-question--release': (X: -132px, Y:-44px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-question': (X: -132px, Y:-88px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-vk--hover': (X: -88px, Y:-88px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-vk--release': (X: -44px, Y:-88px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('icon-vk': (X: -44px, Y:0px, W: 34px, H: 34px, TW: 196px, TH: 160px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

